import bar from '../../components/bar/bar.vue';
export default {
    components:{
		bar
	},
	data(){
		return{
			// 自定义导航栏对象
			setNav:{
				'bg':'#FFFFFF',		// 背景色
				'navTitle': '详情',	// 导航标题
				'leftIcon': 'url(../../static/images/nav_left.png)',	// 左图标
				'rightIcon': 'url(../../static/images/nav_more.png)',	// 右图标
				'rightText': '发布',										// 右文本
				'isdisPlayNavTitle': true,	// 是否显示返回按钮
				'isRightType': 1,			// 右侧标签显示类型 0不显示 1图标 2文本
				// 由于导航栏是共用的，把所有的东西封装好，然后有些页面不需要的东西通过条件控制进行显示与隐藏
			},
			layer: false, // 分享层
			dataList: new Object(), // {}
		}
	},
	onLoad: function (options) {
		// options可以接收父页面(index) 传过来的值
		var lid = options.lid;
		
		let that = this;
		// 显示 loading 提示框
		uni.showLoading({
			title: 'loading...'
		})
		this.$axios.request({
			url: '/login/selectArticleById',
			method: 'post',
			data: {
				"articleId": lid
			}
		}).then(function(res) {
			that.dataList = res.data.data;
			console.log(that.dataList)
			uni.hideLoading()
		})
	},
	methods: {
		rightButton: function (e) {
			// console.log(e) //子组件传递的参数
			uni.showActionSheet({
				// itemList: ['复制文字', '举报', '取消'],
				itemList: ['复制文字', '举报'],
				success: function (res) {
					// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if (res.tapIndex === 1) {
						uni.showActionSheet({
							// itemList: ['违法有害信息', '垃圾广告', '色情低俗', '取消'],
							itemList: ['违法有害信息', '垃圾广告', '色情低俗'],
							success: function (res) {
								console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
							}
						});
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},









		// 用户中心
		userCenter: function(e) {
			var userId = e.currentTarget.dataset.uid;
			uni.navigateTo({
				url: '/pages/user/user?uld='+userId
			})
		},
		comment: function () {
			uni.navigateTo({
				url: '/pages/commentList/commentList',
			})
		}
	},
	computed: {
		// 时间区间判断
		getDateDiff() {
			return function (timeStamp) {
				var minute = 1000 * 60;
				var hour = minute * 60;
				var day = hour * 24;
				var halfamonth = day * 15;
				var month = day * 30;
				var now = new Date().getTime();
				var diffValue = now - timeStamp;
				if(diffValue < 0){return;}
				var monthC =diffValue/month;
				var weekC =diffValue/(7*day);
				var dayC =diffValue/day;
				var hourC =diffValue/hour;
				var minC =diffValue/minute;
				var result = '';
				if(monthC>=1){
					result="" + parseInt(monthC) + "个月前";
				}
				else if(weekC>=1){
					result="" + parseInt(weekC) + "周前";
				}
				else if(dayC>=1){
					result=""+ parseInt(dayC) +"天前";
				}
				else if(hourC>=1){
					result=""+ parseInt(hourC) +"小时前";
				}
				else if(minC>=1){
					result=""+ parseInt(minC) +"分钟前";
				}else {
					result="刚刚";
				}
				return result;
			}
		}
	}
}
